@keyframes rotate45 {
  0% {
    transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg);

    opacity: 1;

  }
  100% {
    transform: rotateY(0deg) rotateY(0deg) rotateZ(45deg) skew(0deg, 0deg);
    transform-style: preserve-3d;
    opacity: 1;
  }
}

@keyframes rotate-45 {
  0% {
    transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg);

    opacity: 1;

  }
  100% {
    transform: rotateY(0deg) rotateY(0deg) rotateZ(-45deg) skew(0deg, 0deg);
    opacity: 1;
    //transform-origin: 20% 40%;

  }
}

.footer {
  height: 400px;
  background-color: black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 100%;

  .logo-container {
    margin-bottom: 30px;
  }

  .options {


    .option {
      padding: 20px;
      color: white;
      &:hover {
        background-color: transparent;
        color: white;
        font-weight: 900;
      }
    }
  }
  .socialIcons {

    .socialIcon{
      height: 40px;
      padding-right: 20px;
      margin-top: 50px;
      &.rotate45 {

      &:hover {
        animation: rotate45 ease 0.5s;
        animation-fill-mode: forwards;

      }
      }
      &.rotate-45 {

        &:hover {
          animation: rotate-45 ease 0.5s;
          animation-fill-mode: forwards;

        }
      }
    }

  }
  .copyrights {
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-top: 50px;
    color: white;
    font-size: small;
      span {
        padding-bottom: 10px;
      }
  }
}

@media screen and (max-width: 1200px) {}

@media screen and (max-width: 768px) {
  .footer {
    height: 100%;
    display: block;
    position: absolute;
    align-items: center;
    width: 100%;
  
    .logo-container {
      margin-bottom: 30px;
      margin-top: 30px;
      display: flex;
      justify-content: center;
    }
  
    .options {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
    }
    .socialIcons {
        display: flex;
        justify-content: center;
  
      .socialIcon{
        height: 40px;
        padding-right: 20px;
        margin-top: 50px;
        &.rotate45 {
  
        &:hover {
          animation: rotate45 ease 0.5s;
          animation-fill-mode: forwards;
        }
        }
        &.rotate-45 {
  
          &:hover {
            animation: rotate-45 ease 0.5s;
            animation-fill-mode: forwards;
          }
        }
      }
  
    }
    .copyrights {
      display: flex;
      flex-direction: column;
      text-align: center;
      margin-top: 50px;
      margin-bottom: 90px;
      color: white;
      font-size: small;

    }
  }
}
