.design-description {
  width : 40%;
  h1 {
    font-size: 2.5rem;
    font-weight: 900;
    margin-bottom: 40px;
  }
}

.image-container-design {
  height: 380px;
  width: 380px;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 4px 4px 20px 12px #d1d1d1;
  .image-design {
    height: 70%;
    object-fit: fill;

  }
}


.title-additionalServices {
  text-align: center;
  margin-bottom: 80px;

  .bold-title {
    font-weight: bolder;
  }

  h2 {
    font-weight: normal;
  }
}

.packaging-grid {
  display: grid;
  grid-auto-columns: 1fr;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto auto;
  grid-row-gap: 16px;
  grid-column-gap: 16px;
  width: 800px;
  margin: 0 auto 150px;
  text-align: center;
  background-color: #ffd629;
  h3 {
    margin-bottom: 30px;
    margin-top: 15px;
    font-size: 30px;
    font-weight: 900;
  }

  .leftGrid {
    padding: 50px;
  }

  .rightGrid {
    padding: 50px;
  }
}

.fa-edit {
  font-size: 50px;
}

.fa-heart {
  font-size: 50px;
}

.card-complementaryServices {
  //background-color: #ffd629;
  position: relative;
  //background-color: black;
  width: 1200px;
  height: 1100px;
  margin: 0 auto;

  .card-complementaryService {
    background-image: linear-gradient(180deg, transparent, rgba(0, 0, 0, 0.9)), url("../../../assests/shootingPhoto.jpg");
    width: 700px;
    height: 544px;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 30px;
    //background-image: linear-gradient(180deg, transparent, rgba(0, 0, 0, 0.9));
    //height: 100%;
    &:hover {
      cursor: pointer;
      transform: scale(1.1);
      transition: transform 2s cubic-bezier(0.25, 0.45, 0.45, 0.95);
    }
  }

  h3 {
    color: #ffd629;
    font-weight: 700;
    font-size: 30px;
    margin-top: 15px;
  }

  p {
    color: white;
  }

}


